@import url("https://fonts.googleapis.com/css2?family=Bigelow+Rules&family=Creepster&display=swap");

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.title {
  font-family: "Creepster", cursive;
  font-size: 4rem;
  color: #0a1924;
  margin-bottom: 10px;
}

h2 {
  font-family: "Creepster", cursive;
  font-size: 2.5rem;
  color: #0a1924;
  margin-bottom: 10px;
}
